"use client";

import React, { useState } from 'react';
import { toolSuggestions } from '@/data/toolSuggestions';
import ToolCard from '@/components/ToolCard';
import { SearchIcon } from '@/components/Icons';

export default function ToolSuggestionsPage() {
    const [selectedCategory, setSelectedCategory] = useState<string>('All');
    const [searchTerm, setSearchTerm] = useState('');

    const categories = ['All', 'Equipment', 'Gadgets', 'Essentials'];

    const filteredTools = toolSuggestions.filter(tool => {
        const matchesCategory = selectedCategory === 'All' || tool.category === selectedCategory;
        const matchesSearch = tool.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
            tool.description.toLowerCase().includes(searchTerm.toLowerCase());
        return matchesCategory && matchesSearch;
    });

    return (
        <div className="bg-brand-background min-h-screen p-6 md:p-10">
            <div className="max-w-6xl mx-auto">
                <header className="mb-10 text-center">
                    <h1 className="text-4xl md:text-5xl font-serif font-bold text-brand-text-title mb-4">
                        Baker's Tool Suggestions
                    </h1>
                    <p className="text-brand-text-body text-lg max-w-2xl mx-auto">
                        Curated recommendations for the best baking equipment, gadgets, and essentials.
                        We've tested these so you don't have to.
                    </p>
                </header>

                {/* Filters and Search */}
                <div className="flex flex-col md:flex-row justify-between items-center gap-4 mb-8 bg-white p-4 rounded-xl shadow-sm border border-brand-border">
                    <div className="flex gap-2 overflow-x-auto pb-2 md:pb-0 w-full md:w-auto no-scrollbar">
                        {categories.map(category => (
                            <button
                                key={category}
                                onClick={() => setSelectedCategory(category)}
                                className={`px-4 py-2 rounded-full text-sm font-medium whitespace-nowrap transition-colors ${selectedCategory === category
                                        ? 'bg-brand-pink text-white'
                                        : 'bg-brand-pink-light/50 text-brand-text-body hover:bg-brand-pink-light'
                                    }`}
                            >
                                {category}
                            </button>
                        ))}
                    </div>

                    <div className="relative w-full md:w-64">
                        <SearchIcon className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-brand-text-body/40" />
                        <input
                            type="text"
                            placeholder="Search tools..."
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="w-full pl-10 pr-4 py-2 rounded-lg border border-brand-border focus:ring-2 focus:ring-brand-pink/50 focus:border-brand-pink outline-none transition-shadow"
                        />
                    </div>
                </div>

                {/* Grid */}
                {filteredTools.length > 0 ? (
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                        {filteredTools.map(tool => (
                            <ToolCard key={tool.id} tool={tool} />
                        ))}
                    </div>
                ) : (
                    <div className="text-center py-20 bg-white rounded-xl border border-brand-border border-dashed">
                        <p className="text-brand-text-body text-lg">No tools found matching your criteria.</p>
                        <button
                            onClick={() => { setSelectedCategory('All'); setSearchTerm(''); }}
                            className="mt-4 text-brand-pink font-semibold hover:underline"
                        >
                            Clear Filters
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
}
